import React, { memo, useState, useEffect } from "react";
import { Grid, Typography, Box, useMediaQuery, useTheme, Paper } from '@mui/material';
import { styled } from '@mui/material/styles';
import { sectionHeaderStyles, sectionContainerStyles } from "../../theme/theme";
import { useAppContext } from '../../context/AppContext';

// Animated skill item component
const SkillItem = styled(Box)(({ theme, delay, color }) => ({
  padding: '10px',
  borderRadius: '8px',
  backgroundColor: theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.05)' : 'rgba(0, 0, 0, 0.03)',
  transition: 'all 0.3s ease',
  border: `1px solid ${theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.08)'}`,
  display: 'flex',
  alignItems: 'center',
  position: 'relative',
  overflow: 'hidden',
  opacity: 0,
  animation: 'slideIn 0.6s forwards',
  animationDelay: `${delay}s`,
  '&:hover': {
    transform: 'translateX(5px)',
    backgroundColor: theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.05)',
    boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
    '& .skill-icon': {
      transform: 'rotate(10deg) scale(1.2)',
    },
    '& .skill-bg': {
      opacity: 0.2,
    }
  },
  '&::before': {
    content: '""',
    position: 'absolute',
    left: '-100%',
    top: 0,
    width: '100%',
    height: '100%',
    background: `linear-gradient(90deg, transparent, rgba(${color}, 0.1), transparent)`,
    animation: 'shimmer 2s infinite',
    animationDelay: `${delay + 1}s`,
  },
  '@keyframes slideIn': {
    from: { transform: 'translateX(-20px)', opacity: 0 },
    to: { transform: 'translateX(0)', opacity: 1 }
  },
  '@keyframes shimmer': {
    '0%': { left: '-100%' },
    '100%': { left: '100%' }
  }
}));

// Animated skill category
const CategoryHeading = styled(Typography)(({ theme, delay }) => ({
  marginBottom: '1.5rem',
  color: theme.palette.text.primary,
  fontWeight: 600,
  position: 'relative',
  opacity: 0,
  animation: 'fadeIn 0.5s forwards',
  animationDelay: `${delay}s`,
  '&::after': {
    content: '""',
    position: 'absolute',
    bottom: '-5px',
    left: 0,
    width: '50px',
    height: '3px',
    background: theme.palette.primary.main,
    transform: 'scaleX(0)',
    transformOrigin: 'left',
    transition: 'transform 0.3s ease',
    animation: 'scaleIn 0.5s forwards',
    animationDelay: `${delay + 0.2}s`,
  },
  '@keyframes fadeIn': {
    from: { opacity: 0, transform: 'translateY(-10px)' },
    to: { opacity: 1, transform: 'translateY(0)' }
  },
  '@keyframes scaleIn': {
    from: { transform: 'scaleX(0)' },
    to: { transform: 'scaleX(1)' }
  }
}));

// Skill icon with animations
const SkillIcon = styled(Box)(({ theme }) => ({
  marginRight: '15px',
  fontSize: '1.5rem',
  transition: 'transform 0.3s ease',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

// Background element for the skill
const SkillBackground = styled(Box)(({ color, theme }) => ({
  position: 'absolute',
  right: '10px',
  bottom: '0',
  fontSize: '3rem',
  opacity: 0.05,
  color: color || theme.palette.primary.main,
  zIndex: 0,
  transition: 'opacity 0.3s ease, transform 0.3s ease',
}));

// Skills data with hex colors for visual enhancement
const skillsData = {
  languages: [
    { name: "HTML", emoji: "üåê", color: "#E34F26", rgb: "227, 79, 38" },
    { name: "CSS", emoji: "üé®", color: "#1572B6", rgb: "21, 114, 182" },
    { name: "JavaScript", emoji: "‚ö°", color: "#F7DF1E", rgb: "247, 223, 30" },
    { name: "TypeScript", emoji: "üõ†Ô∏è", color: "#3178C6", rgb: "49, 120, 198" },
    { name: "Python", emoji: "üêç", color: "#3776AB", rgb: "55, 118, 171" },
  ],
  frameworks: [
    { name: "React", emoji: "‚öõÔ∏è", color: "#61DAFB", rgb: "97, 218, 251" },
    { name: "React Native", emoji: "üì±", color: "#61DAFB", rgb: "97, 218, 251" },
    { name: "Django", emoji: "üêç", color: "#092E20", rgb: "9, 46, 32" },
  ],
  databases: [
    { name: "PostgreSQL", emoji: "üêò", color: "#336791", rgb: "51, 103, 145" },
    { name: "SQL", emoji: "üóÑÔ∏è", color: "#4479A1", rgb: "68, 121, 161" },
    { name: "MySQL", emoji: "üê¨", color: "#4479A1", rgb: "68, 121, 161" },
    { name: "NoSQL", emoji: "üìÇ", color: "#4DB33D", rgb: "77, 179, 61" },
  ],
  tools: [
    { name: 'Share Hosting', emoji: 'üì°', color: '#795548', rgb: "121, 85, 72" },
    { name: 'Visual Studio', emoji: 'üñ•Ô∏è', color: '#007ACC', rgb: "0, 122, 204" },
    { name: 'Sublime Text', emoji: 'üìù', color: '#FF9800', rgb: "255, 152, 0" },
  ],
  cloud: [
    { name: "Azure", emoji: "‚òÅÔ∏è", color: "#0089D6", rgb: "0, 137, 214" },
    { name: "Digital Ocean", emoji: "üåä", color: "#0080FF", rgb: "0, 128, 255" },
  ],
  os: [
    { name: "Mac", emoji: "üçè", color: '#607D8B', rgb: "96, 125, 139" },
    { name: "Windows", emoji: "üñ•Ô∏è", color: '#0078D6', rgb: "0, 120, 214" },
    { name: "Linux", emoji: "üêß", color: '#FCC624', rgb: "252, 198, 36" },
  ],
};

// Memoized skill category component
const SkillCategory = memo(({ title, skills, delay = 0 }) => {
  const theme = useTheme();
  const isDarkMode = theme.palette.mode === 'dark';
  const { animationsEnabled } = useAppContext();
  
  // If animations are disabled, reset delays
  const actualDelay = animationsEnabled ? delay : 0;
  
  return (
    <Box sx={{ mb: 4 }}>
      <CategoryHeading 
        variant="h6" 
        delay={actualDelay}
      >
        {title}
      </CategoryHeading>
      
      <Box component="ul" sx={{ 
        listStyleType: 'none', 
        padding: 0,
        margin: 0,
        display: 'flex',
        flexDirection: 'column',
        gap: 1.5,
      }}>
        {skills.map((skill, index) => (
          <Box
            component="li"
            key={index}
            sx={{ position: 'relative' }}
          >
            <SkillItem 
              delay={actualDelay + (index * 0.1)} 
              color={skill.rgb}
            >
              <SkillIcon className="skill-icon">
                <span>{skill.emoji}</span>
              </SkillIcon>
              
              <Typography sx={{ fontWeight: 500, zIndex: 1 }}>
                {skill.name}
              </Typography>
              
              <SkillBackground 
                className="skill-bg"
                color={skill.color}
              >
                {skill.emoji}
              </SkillBackground>
            </SkillItem>
          </Box>
        ))}
      </Box>
    </Box>
  );
});

// Main Skills component
const Skills = memo(() => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));
  const [isVisible, setIsVisible] = useState(false);
  const { animationsEnabled } = useAppContext();
  
  // Observer to trigger animations when section is in view
  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          observer.disconnect();
        }
      },
      { threshold: 0.1 }
    );
    
    const element = document.getElementById('skills-section');
    if (element) {
      observer.observe(element);
    }
    
    return () => {
      observer.disconnect();
    };
  }, []);
  
  // Animation for the image
  const imageAnimation = {
    opacity: 0,
    animation: isVisible && animationsEnabled ? 'fadeIn 0.8s forwards' : 'none',
    '@keyframes fadeIn': {
      from: { opacity: 0, transform: 'scale(0.9)' },
      to: { opacity: 1, transform: 'scale(1)' }
    }
  };
  
  return (
    <Box id="skills-section" sx={sectionContainerStyles(theme)}>
      <Typography 
        variant="h4" 
        gutterBottom 
        sx={{
          ...sectionHeaderStyles,
          opacity: isVisible ? 1 : 0,
          transition: 'opacity 0.5s ease, transform 0.5s ease',
          transform: isVisible ? 'translateY(0)' : 'translateY(20px)',
        }}
      >
        <span style={{ color: theme.palette.primary.main }}>#</span> skills
      </Typography>

      <Grid container spacing={4} sx={{ mt: 2 }}>
        {/* Skills image (left column on desktop) */}
        <Grid item xs={12} md={4}>
          <Box 
            sx={{
              borderRadius: '12px',
              overflow: 'hidden',
              height: '100%',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              position: 'relative',
              ...imageAnimation
            }}
          >
            <Paper elevation={6} sx={{
              p: 2,
              borderRadius: '12px',
              background: theme.palette.mode === 'dark' 
                ? 'linear-gradient(145deg, #1a1a1a, #2a2a2a)' 
                : 'linear-gradient(145deg, #fff, #f0f0f0)',
              position: 'relative',
              '&::before': animationsEnabled ? {
                content: '""',
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
                background: `linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent)`,
                transform: 'translateX(-100%)',
                animation: isVisible ? 'shimmerEffect 3s infinite' : 'none',
                zIndex: 1,
              } : {},
              '@keyframes shimmerEffect': {
                '0%': { transform: 'translateX(-100%)' },
                '100%': { transform: 'translateX(100%)' }
              }
            }}>
              <img 
                src="assets/Group36.png" 
                alt="Skills Illustration" 
                style={{
                  maxWidth: '100%',
                  height: 'auto',
                  objectFit: 'contain',
                  filter: theme.palette.mode === 'dark' ? 'drop-shadow(0 0 10px rgba(199, 120, 221, 0.3))' : 'none',
                }} 
                loading="lazy"
              />
            </Paper>
          </Box>
        </Grid>
        
        {/* Skills categories (right column on desktop) */}
        <Grid item xs={12} md={8}>
          <Grid container spacing={2}>
            {Object.entries(skillsData).map(([category, skills], index) => (
              <Grid item xs={12} sm={6} md={4} key={index}>
                <SkillCategory 
                  title={category.charAt(0).toUpperCase() + category.slice(1)} 
                  skills={skills}
                  delay={0.2 + index * 0.1}
                />
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Grid>
    </Box>
  );
});

export default Skills;